import React from "react";
import { styled_section } from "../commonStyles";
import "./TeamInfo.css";

const Section = styled_section();

class TeamInfo extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      idx: 0,
      info: [
        {
          name: "",
          image: "image.png",
          title: " ,   ",
          content:
            "     .     ,    .",
        },
        {
          name: "",
          image: "image.png",
          title: ",       ",
          content:
            "  ,  , , UI     .",
        },
        {
          name: "",
          image: "image.png",
          title: "     ",
          content:
            "   .   LUA  UNITY   . 4     .",
        },
      ],
    };
  }

  render() {
    return (
      <Section id="Team">
        <h2>NPC  </h2>
        <span>
          NPC <b> , , </b>  
          .
        </span>
        <div className="teamList">
          {this.state.info.map((e, idx) => {
            if (this.state.idx === idx) {
              return <div className="selected">{e.name}</div>;
            }
            return (
              <div
                onClick={() => {
                  this.setState({ ...this.state, idx });
                }}
              >
                {e.name}
              </div>
            );
          })}
        </div>
        <div className="teamInfo">
          <img />
          <h3>{this.state.info[this.state.idx].title}</h3>
          <span>{this.state.info[this.state.idx].content}</span>
          <div className="teamInfo_nav">
            <iconify-icon
              icon="ph:caret-left-bold"
              onClick={() => {
                const idx = this.state.idx > 0 ? this.state.idx - 1 : 0;
                this.setState({ ...this.state, idx });
              }}
            />
            <iconify-icon
              icon="ph:caret-right-bold"
              onClick={() => {
                const idx = this.state.idx < 2 ? this.state.idx + 1 : 2;
                this.setState({ ...this.state, idx });
              }}
            />
          </div>
        </div>
        <div className="teamInfo_all">
          {this.state.info.map((team) => (
            <div className="teamInfo">
              <img />
              <h3>{team.title}</h3>
              <span>{team.content}</span>
            </div>
          ))}
        </div>
      </Section>
    );
  }
}

export default TeamInfo;
